import { getLikesCard, deleteUserCard, deleteLikeCard } from './api.js';
import { openPopupImage } from './index.js';


const cardTemplate = document.querySelector('#card-template').content;


export function createCard(cardData, userId, deleteCard, addLikeCard) {
  const cardElement = cardTemplate.querySelector('.places__item').cloneNode(true);
  const cardImage = cardElement.querySelector('.card__image');
  const cardTitle = cardElement.querySelector('.card__title');
  const deleteButton = cardElement.querySelector('.card__delete-button');
  const cardLikeButton = cardElement.querySelector('.card__like-button');
  const cardLikeValue = cardElement.querySelector('.card__like-value');
  const cardId = cardData._id;

  cardTitle.textContent = cardData.name;
  cardImage.src = cardData.link;
  cardImage.alt = cardData.name;
  

  if (cardData.likes.length) {
    cardLikeValue.textContent = cardData.likes.length;
  }

  if (cardData.likes.some((like) => like._id === userId)) {
    cardLikeButton.classList.add("card__like-button_is-active");
  }

  if (userId != cardData.owner._id) {
    deleteButton.remove();
  } else {
    deleteButton.addEventListener("click", () => {
      deleteCard(cardElement, cardId);
    });
  };
  

  cardLikeButton.addEventListener('click', (evt) =>  addLikeCard(evt, cardData));


  cardImage.addEventListener('click', () => openPopupImage(cardData.link, cardData.name));


  return cardElement;
};


export function deleteCard(cardData, cardId) {
  deleteUserCard(cardId)
    .then(() => {
      cardData.target.closest('.places__item').remove()
    })
    .catch((error) => {
      console.log(error)
    })
};


export function addLikeCard(cardData, cardId) {
  const cardLikeButton = cardData.closest('.card__description');
  const cardLikeValue = cardLikeButton.querySelector('.card__like-value');

  if(cardData.classList.contains('card__like-button_is-active')){
    deleteLikeCard(cardId, "DELETE")
      .then(res => {
        cardLikeValue.textContent = res.likes.length;
        cardData.classList.toggle('card__like-button_is-active');
      })
      .catch((error) => {
        console.log(error)
      })
  } else {
    getLikesCard(cardId, "PUT")
      .then(res => {
        cardLikeValue.textContent = res.likes.length;
        cardData.classList.toggle('card__like-button_is-active');
      })
      .catch((error) => {
        console.log(error)
      })
  }
};


function checkLikeById(id, array) {
  const idArray = array.map(cardData => cardData._id)
  return idArray.includes(id)
};
